/* FONT FAMILY & FONT WEIGHTS */
:root {
  --font-body: Arial, sans-serif;
  --font-weight-standard: 500;
  --font-weight-heavy: 700;
  --font-weight-heaviest: 900;
}

/* COLORS */
:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --pale-blue: hsl(225, 100%, 94%);
  --bright-blue: hsl(245, 75%, 52%);
  --desaturated-blue: hsl(224, 23%, 55%);
  --very-desaturated-blue: hsl(225, 100%, 98%);
  --dark-blue: hsl(223, 47%, 23%);
}

/* Boilerplate styles to apply to the page as a whole */
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--pale-blue);
}

.container {
  background-color: var(--white);
}

/* Plan & Pricing BG Color */
.plan {
  background-color: var(--very-desaturated-blue);
}

body {
  text-align: center;
}

/* HEADER BASE VALUES */
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--dark-blue);
}

/* TITLE FONT SIZE */
.order-sum {
  font-size: 22px;
  padding-top: 2rem;
}

/* CANCEL & PARA BUTTONS */
.cancel, p {
  color: var(--desaturated-blue);
}

.cancel {
  text-decoration: none;
}

/* FLEXBOX & BORDER RADIUS */
.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
  width: 85%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  top: 4em;
  border-radius: 0 0 10px 10px;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}
.container .submit-plan {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem 0 1.5rem 0;
}
.container .submit-plan .proceed {
  width: 13rem;
  padding: 0.65rem 0 0.65rem 0;
  border-radius: 8px;
  border: none;
  color: var(--white);
  background-color: var(--bright-blue);
  font-weight: var(--font-weight-heaviest);
}
.container .submit-plan .cancel {
  padding-top: 2rem;
  font-weight: var(--font-weight-heavy);
}

/* HEADER IMG MAX WIDTH */
.title-pic {
  max-width: 272px;
}

.plan {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-left: 1em;
  padding-right: 1em;
  border-radius: 10px;
}
.plan img, .plan a {
  position: relative;
}
.plan img {
  width: 25%;
  right: 0.5em;
}
.plan a {
  left: 0.75em;
}

.plan-price {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.plan-price .plan-title {
  margin-bottom: -15px;
}

/* MEDIA QUERIES */
@media (min-width: 375px) {
  .title-pic {
    max-width: 318px;
    width: 318px;
  }

  .container {
    border-radius: 10px;
  }
}
@media (min-width: 414px) {
  .title-pic {
    max-width: 351px;
    width: 351px;
  }
}
@media (min-width: 510px) {
  .title-pic {
    max-width: 433px;
    width: 433px;
  }

  .container {
    top: 2.6rem;
  }
}
@media (min-width: 600px) {
  .title-pic {
    max-width: 510px;
    width: 510px;
  }

  .description {
    width: 60%;
    position: relative;
    left: 6rem;
  }

  .container {
    top: 1.5em;
  }
}
@media (min-width: 768px) {
  .title-pic {
    max-width: 652px;
    width: 652px;
  }

  .container {
    top: 0.7em;
  }
  .container .submit-plan {
    padding-bottom: 0;
  }
  .container .cancel {
    position: relative;
    bottom: 0.85rem;
  }
}
@media (min-width: 800px) {
  .title-pic {
    max-width: 680px;
    width: 680px;
  }

  .order-sum {
    padding-top: 0.45rem;
  }
}
@media (min-width: 1280px) {
  .container {
    max-width: 680px;
    width: 680px;
  }
  .container .plan-price {
    padding-right: 13rem;
    width: 100%;
  }
  .container .proceed {
    width: 26rem !important;
  }
}
@media (min-width: 1440px) {
  .container {
    top: 6.5rem;
  }
}
.title-pic {
  border-radius: 10px 10px 0 0;
}

.proceed:hover {
  opacity: 0.55;
  cursor: pointer;
}